@import url(//at.alicdn.com/t/font_1656641_i54wuos68no.css);
@font-face{font-family:"FZKTJW";src:url(../../static/media/FZKTJW.439f3503.TTF) format("truetype");font-style:normal;font-weight:normal}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App {
  text-align: center;
  /* max-width: 900px; */
  margin: auto;
  background-color: #fffbf2;
}
.words-input{height:30px;width:95%;font-size:20px;border:1px solid #ff6600;padding:4px 20px;font-family:cursive;border-radius:10px;box-shadow:0px 0px 4px #ff6600}.copybook-page{border:1px solid red;padding:4px;margin:8px}.copybook-page-box{display:flex;flex-wrap:wrap;padding:4px;width:100%;border:2px solid red}.my-radio{padding:2.5px;border:1px solid #ccc;border-radius:50%;margin-right:5px}

.icon-help {
  font-size: 28px;
  margin-right: 8px;
  color: #dddddd;
}
.help-weixi-code {
  background-image: url(../../static/media/IMG_7308.3f86895a.JPG);
  width: 300px;
  height: 300px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin: auto;
}

.copybook-print{
  /* width: 1240px;
  height: 1757px; */
  /* padding: 40px; */
  display: none;
}

@media print{
  .copybook-print{
    display: block;
  }
  .am-tabs-tab-bar-wrap{
    display: none;
  }
  .App{
    display: none;
  }
}

